
.PlBookDetail {
  overflow: hidden;
  height: 100%;
  width: 100%;
  padding: 0.5rem 0 0.5rem 0;
  box-sizing: border-box;
}
.PlBookDetail-book {
  box-sizing: border-box;
  padding: 1rem 0;
  background-color: #fff;
  height: 70%;
  width: 90%;
  margin: 0 auto;
}
.PlBookDetail-title {
  text-align: center;
  font-size: 24px;
  color: #3498DB;
  font-weight: bold;
  height: 25%;
}
.PlBookDetail-meta {
  color: #2892D9;
  height: 25%;
  padding-left: 2.6rem;
  overflow: hidden;
}
.PlBookDetail-text {
  display: inline-block;
  padding-left: 0.3rem;
  font-size: 18px;
}
.PlBookDetail-btn {
  height: 30%;
  box-size: border-box;
  padding-top: 1rem;
}
.PlBookDetail-btn-delete {
  background-color: #E74C3C;
  margin-top: 0.5rem;
}
.PlBookDetail-btn-back {
  background-color: #3498DB;
}
button {
  display: block;
  width: 90%;
  margin: 0 auto;
  border: none;
  font-size: 20px;
  height: 1.2rem;
  color: #fff;
}
button:active {
  opacity: 0.8 !important;
}



